import * as RAPIER from '@dimforge/rapier3d';

/**
 *  Rapier.js   API
 * @returns {Promise<Object>} -  RAPIER  
 */
export async function initRapier() {
    console.log('  Rapier 3D  ...');
    await RAPIER.init();
    console.log(' Rapier 3D ');
    return RAPIER;
}

/**
 *    RAPIER (   )
 */
export function getRapier() {
    return RAPIER;
}
